















export default {
	name: "tem-items",
	props: {
		template: { 
			type: Object,
			required: true 
		}
	},
	data() {
		return {
			list: []
		};
	},
	onLoad() {
		var temp = [];
		
		for (var i = 0; i < this.template.lineNumber * 2; i++) {
			temp.push(this.template.list[i]);
			
			if ((i > 0 && ((i + 1) % this.template.lineNumber) == 0)) {
				this.list.push(temp);
				temp = [];
			}
			
			if (i == this.template.list.length - 1) break;
		}
		
		console.log(this.list);
	},
	methods: {
		clickItem(item) {
			this.router(item.action, () => {
				
			})
		}
	}
}



