



















import loadMore from '../../components/load-more.vue'

export default {
	name: "tem-page",
	components: {
		loadMore
	},
	props: {
		template: { 
			type: Object,
			required: true 
		}
	},
	data() {
		return {
			list: [],
			loadingType: 0,
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			}
		};
	},
	onLoad() {
		this.list = this.template.list;
	},
	onReachBottom() {
		if (this.loadingType != 0 || !this.template.haveMore) {
			return;
		}
		this.loadingType = 1;
		for (let i = 0; i < 4; i++) {
			this.list.push(this.list[0]);
		}
		setTimeout(() => {
			if (this.list.length >= 18) {
				this.loadingType = 2;
				return;
			}
			this.loadingType = 0;
		}, 800);
	},
	methods: {
		clickItem(url) {
			this.router(url, () => {
				
			})
		}
	}
}



